export function HomeAboutContent(props) {
  const { children, isPhone } = props;

  return (
    <HomeAboutContentStyled isPhone={isPhone}>
      <AnimationOnScroll {...anim(props)}>{children}</AnimationOnScroll>
    </HomeAboutContentStyled>
  );
}

export function HomeAboutTitle(props) {
  const { children, img } = props;

  return (
    <AnimationOnScroll {...anim(props)}>
      <HomeAboutTitleStyled img={img}>{children}</HomeAboutTitleStyled>
    </AnimationOnScroll>
  );
}

export function HomeAboutOrg(props) {
  const { img, url, target } = props;

  return (
    <HomeAboutOrgStyled>
      <img src={img} alt="" />
      <Link href={url} target={target}>
        <img src={OrgBg} alt="" />
      </Link>
    </HomeAboutOrgStyled>
  );
}

